import React, { useState } from "react";
import Option from "./Option";
import styled from "styled-components";
import { winner } from "./Winner";
import TwitterIcon from "@mui/icons-material/Twitter";
import spinWheelSound from ".././images/spinWheel.wav";
import success from ".././images/success.mp3";
const Wh = () => {
  const [random, setRandom] = useState(0);
  const [degrees, setDegrees] = useState(0);
  const [spin, setSpin] = useState(false);
  const [active, setActive] = useState(false);
  const [result, setResult] = useState(0);
  const [total, setTotal] = useState(0);
  const spinSound = new Audio(spinWheelSound);
  const Success = new Audio(success);
  const options = JSON.parse(localStorage.getItem("options") || "{}");
  const [val] = useState({
    val1: options.val1 || "-100000 Croc",
    val2: options.val2 || "+250000 Croc",
    val3: options.val3 || "+500000 Croc",
    val4: options.val4 || "+1000000 Croc",
    val5: options.val5 || "+10M Jackpot",
  });
  const [val1] = useState({
    val1: options.val1 || -100000,
    val2: options.val2 || 250000,
    val3: options.val3 || 500000,
    val4: options.val4 || 1000000,
    val5: options.val5 || 10000000,
  });
  const arr = new Array(24).fill("");

  const handlePull = () => {
    if (sum >= 100000) {
      setRandom(getRandom(2160, 2520));
    } else {
      alert("insufficient balance");
    }
  };

  const handleRelease = () => {
    if (sum >= 100000) {
      setSpin(true);
      setActive(false);
      spinSound.play();
      setSum(sum - 100000);
      setTimeout(() => {
        setSpin(false);
        setDegrees(degrees + random);
        setResult(checkWinner((degrees + random) % 360));
        setRandom(0);
        if (total <= 0) {
          setTotal(total + checkWinner((degrees + random) % 360));
          setSum(sum - 100000 + checkWinner((degrees + random) % 360));
          Success.play();
        } else {
          setTotal(total + checkWinner((degrees + random) % 360));
          setSum(sum - 100000 + checkWinner((degrees + random) % 360));
          Success.play();
        }
      }, 16000);
    } else {
      alert("Insufficient balance");
    }
    setResult("");
  };

  function getRandom(min, max) {
    return Math.floor(Math.random() * (max - min) + min);
  }

  function checkWinner(num) {
    for (let i = 0; i < winner.length; i++) {
      for (let j = 0; j < winner[i].length; j++) {
        if (num > winner[i][j][0] && num < winner[i][j][1]) {
          return i === 0
            ? val1.val1
            : i === 1
            ? val1.val2
            : i === 2
            ? val1.val3
            : i === 3
            ? val1.val4
            : val1.val5;
        }
      }
    }
  }

  const [sum, setSum] = useState(0);

  const handleDepositClick = () => {
    const currentBalance = sum + 100000;
    setSum(currentBalance);
  };
  const handleClaimClick = () => {
    if (total === 0) {
      alert(
        "!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!\ncurrent Winning amount is 0\n!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!\n"
      );
    } else {
      const currentBalance = sum + total;
      setSum(currentBalance);
      alert("Croc Claimed");
      setTotal(0);
    }
  };

  return (
    <Container>
      <WheelContainer>
        <Arrow></Arrow>
        <Wheel degrees={degrees} random={random} className={spin ? "spin" : ""}>
          {arr.map((a, i) => (
            <Option
              key={i}
              index={i}
              val={
                i % 2
                  ? val.val1
                  : i % 4
                  ? val.val2
                  : i % 4 === 0 && i % 8 !== 0
                  ? val.val3
                  : i % 8 === 0 && i !== 0 && i % 24 !== 0
                  ? val.val4
                  : i === 0
                  ? val.val5
                  : ""
              }
              color={
                i % 2
                  ? "khaki"
                  : i % 4
                  ? "mediumseagreen"
                  : i % 4 === 0 && i % 8 !== 0
                  ? "cornflowerblue"
                  : i % 8 === 0 && i !== 0 && i % 24 !== 0
                  ? "mediumorchid"
                  : i === 0
                  ? "coral"
                  : ""
              }
            />
          ))}
        </Wheel>
        <LeverContainer
          spin={spin}
          onMouseDown={() => {
            setActive(true);
            handlePull();
          }}
          onMouseUp={handleRelease}
        >
          <Lever active={active}>
            <Handle spin={spin}></Handle>
            <Tube spin={spin}></Tube>
          </Lever>
        </LeverContainer>
      </WheelContainer>
      <Wrapper>
        <h3>For Spinnig: 100000 Croc</h3>
        <span>
          <Button></Button>
          <Button></Button>
        </span>
        <span>
          <h3>Balance: {sum} Croc</h3>
          <Button onClick={handleDepositClick}>Deposit</Button>
        </span>
        <span>
          <h3>Winning amount: {total} Croc</h3>
        </span>
        <span>
          <Button onClick={handleClaimClick}>Claim</Button>
        </span>
      </Wrapper>
      <Board>
        <p className={!spin ? "textAnim" : ""}>{result} croc</p>
      </Board>
      <ActiveLink
        href="https://twitter.com/aicrocmemecoin"
        target="_blank"
        active={active}
      >
        <TwitterIcon /> @Aicroc
      </ActiveLink>
    </Container>
  );
};

export default Wh;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 250px;
  max-width: 100vw;
  position: absolute;
  background-color: #ffffffc8;
  padding: 20px 0;
  border-radius: 20px 50px;
  z-index: 2;
  left: 10px;
  > span {
    font-weight: bold;
    margin-left: 10px;
  }
`;

const Button = styled.button`
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 16px 32px;
  border-radius: 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
  :hover {
    background-color: #45a049;
    color: white;
  }
`;

const Container = styled.div`
  display: grid;
  place-content: center;
  height: 100vh;
  overflow: hidden;
  position: relative;
`;
const WheelContainer = styled.div`
  position: relative;
  z-index: 1;
  width: 66vh;
  bottom: 65px;
  height: 66vh;
  @media screen and (max-width: 800px) {
    transform: scale(0.8);
  }
  @media screen and (max-width: 617px) {
    transform: scale(0.6);
  }
`;
const Arrow = styled.div`
  position: absolute;
  z-index: 12;
  width: 7%;
  height: 7%;
  top: -1vh;
  left: 50%;
  transform: translate(-50%, -50%);
  clip-path: polygon(0% 0%, 100% 0, 100% 50%, 50% 100%, 0 50%);
  background-image: linear-gradient(to bottom, cyan, blue);
`;
const Wheel = styled.div`
  transform: rotate(${(props) => props.degrees}deg);
  background-image: radial-gradient(
    darkgray 0.5vh,
    cyan 1vh,
    green 2vh,
    darkgray 2vh
  );
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1vh solid darkgray;
  outline-offset: -1vh;
  outline: 1.2vh dotted red;
  &.spin {
    animation: spinning 15.5s forwards ease;
    @keyframes spinning {
      7% {
        transform: rotate(${(props) => props.degrees - 15}deg);
      }
      100% {
        transform: rotate(${(props) => props.degrees + props.random}deg);
      }
    }
  }
  @keyframes bording {
    0% {
      outline: 1.2vh dotted red;
    }
    20% {
      outline: 1.2vh dotted blue;
    }
    40% {
      outline: 1.2vh dotted green;
    }
    60% {
      outline: 1.2vh dotted purple;
    }
    80% {
      outline: 1.2vh dotted orange;
    }
    100% {
      outline: 1.2vh dotted yellow;
    }
  }
`;
const LeverContainer = styled.div`
  position: absolute;
  width: 10vh;
  height: 10vh;
  z-index: -1;
  top: 50%;
  right: -20%;
  transform: translate(-50%, -50%);
  pointer-events: ${(props) => (props.spin ? "none" : "all")};
`;
const Lever = styled.div`
  width: 100%;
  height: 100%;
  transition: all ${(props) => (props.active ? 0.2 : 0.5)}s linear;
  transform: rotate(${(props) => (props.active ? 20 : 0)}deg)
    translateY(${(props) => (props.active ? 7 : 0)}vh);
`;
const Handle = styled.div`
  width: 30%;
  height: 30%;
  cursor: ${(props) => (props.spin ? "not-allowed" : "pointer")};
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  background-image: ${(props) =>
    props.spin
      ? "radial-gradient(farthest-corner at 10px 10px, white, black)"
      : "radial-gradient(farthest-corner at 10px 10px, #01da01, black)"};
`;
const Tube = styled.div`
  cursor: ${(props) => (props.spin ? "not-allowed" : "pointer")};
  width: 5%;
  transform: rotate(45deg);
  transform-origin: bottom center;
  height: 110%;
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(to right, white, #310000);
`;
const Board = styled.div`
  height: 10vh;
  width: 30vh;
  position: absolute;
  background-color: #ffffff;
  border-radius: 0% 20px;
  bottom: 85px;
  left: 43%;
  margin: 0 auto;
  display: grid;
  place-content: center;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 3vh;
  box-shadow: inset 0 0 10px 0px black;
  border: 5px solid #303030;
  > p {
    &.textAnim {
      animation: texting 0.5s ease-in;
      @keyframes texting {
        50% {
          font-size: 4vh;
          color: cyan;
        }
        100% {
          font-size: 3vh;
          color: black;
        }
      }
    }
  }
  @media screen and (max-width: 800px) {
    bottom: 85px;
    left: 37%;
  }
`;

const ActiveLink = styled.a`
  position: absolute;
  bottom: 75px;
  right: 0;
  /* color: lightgray; */
  text-decoration: none;
  color: ${(props) => (props.active ? "red" : "blue")};
  font-size: 0.7rem;
  margin: 5px;
  > svg {
    font-size: 0.9rem;
    margin-bottom: -3px;
  }
`;
